\n    background-color: $input-disabled-color;\n    border-color: $input-disabled-solid-color;\n  }\n}\n\n// Hidden input fields\n.wpcf7-latlong, input[name=\"datetime\"] {\n  display: none;\n}\n\n.flatpickr-calendar {\n  border-radius: 0;\n\n  .numInputWrapper {\n    &:hover {\n      background: rgba($shadow, 0.1);\n    }\n  }\n\n  input.numInput {\n    font-size: inherit;\n  }\n\n  .flatpickr-day {\n    &.selected {\n      background: $aqua;\n      border-color: $aqua-dark;\n    }\n\n    &:hover {\n      background: rgba($shadow, 0.1);\n    }\n  }\n\n  .flatpickr-time {\n    height: auto !important;\n    max-height: none;\n\n    .numInputWrapper {\n      height: auto;\n    }\n  }\n}\n\ndiv[id=\"map_canvas\"] {\n  width: 100% !important;\n\n  .gm-style-pbt {\n    text-align: center;\n  }\n}\n",".modal {\n  border: 2px solid $navy;\n  background: $ice;\n  border-radius: 0;\n  max-height: 100%;\n\n  body.admin-bar & {\n    max-height: calc(100% - 32px);\n  }\n\n  .modal-content {\n    padding: 20px;\n\n    img {\n      width: 100%;\n      border: 1px solid $navy;\n    }\n  }\n\n  h3 {\n    margin-bottom: 0;\n  }\n\n  .collection {\n    border: none;\n    background: white;\n    box-shadow: inset 0 0 0 1px $shadow;\n\n    .collection-item {\n      background: none;\n    }\n  }\n\n  .modal-footer {\n    background: $ice;\n    height: auto;\n    padding-bottom: 10px;\n    overflow: hidden;\n  }\n}\n",".obs-meta {\n  margin: 0;\n  padding: 0;\n\n  li {\n    font-size: 12px;\n    display: inline-block;\n    padding-left: 20px;\n\n    &:first-child {\n      padding-left: 0;\n    }\n  }\n\n  .material-icons {\n    font-size: 18px;\n    vertical-align: top;\n  }\n}\n\n.observation.card {\n  box-shadow: 8px 8px $shadow;\n  border-radius: 0;\n  margin: 20px 0;\n  position: relative;\n  color: $navy;\n\n  a.mega-link {\n    display: block;\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    z-index: 100;\n  }\n\n  img {\n    border: 2px